import React, { Component } from 'react'
import './newsitem.css'
export class NewsItems extends Component {
    render() {
let {title, description, ImageUrl, newsUrl} = this.props
        return (
            <div>
                <div className="card m-3" style={{width: "18rem"}}>
                    <img src={!ImageUrl?'https://www.google.com/imgres?imgurl=https%3A%2F%2Fwww.freeiconspng.com%2Fthumbs%2Fno-image-icon%2Fno-image-icon-15.png&imgrefurl=https%3A%2F%2Fwww.freeiconspng.com%2Fimages%2Fno-image-icon&tbnid=ZqUQB6tr_Rkh0M&vet=12ahUKEwj_0-zdze3yAhWB44UKHUp0AaYQMygBegUIARCyAQ..i&docid=McX7zI2MyXyP-M&w=200&h=200&q=no%20image%20available%20png&ved=2ahUKEwj_0-zdze3yAhWB44UKHUp0AaYQMygBegUIARCyAQ':ImageUrl} className="card-img-top imgCard" alt="..."/>
                    <div className="card-body">
                        <h5 className="card-title">{!title?"Title":title}...</h5>
                        <p className="card-text">{!description?"no description":description}...</p>
                        <a href={newsUrl} target="_blank" className="btn btn-sm btn-primary">Read more</a>
                    </div>
                </div>
            </div>
        )
    }
}



export default NewsItems
